.ui-input {
  border: 1px solid #c5c5c5;
  max-width: 300px;
  width: 100%;
  min-height: 40px;
  padding: 0px 5px;
  outline: none;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 2px #cccccc;
  font-size: 14px;
  color: #474747;
  height: 100%;
  text-align: left; }
  .ui-input.rounded {
    border-radius: 25px; }
  .ui-input:focus {
    outline: none; }
  .ui-input.not-valid {
    border-color: red; }
  .ui-input.file {
    width: inherit !important;
    top: 5px;
    position: relative; }

.file-input-container {
  position: relative;
  cursor: pointer; }
  .file-input-container .file-custom {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 5;
    padding: .5rem 1rem;
    line-height: 1.5;
    color: #555;
    background-color: #fff;
    border: .075rem solid #ddd;
    border-radius: .25rem;
    box-shadow: inset 0 0.2rem 0.4rem rgba(0, 0, 0, 0.05);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
    .file-input-container .file-custom:before {
      position: absolute;
      top: -.075rem;
      right: -.075rem;
      bottom: -.075rem;
      z-index: 6;
      display: block;
      content: "Browse";
      padding: .5rem 1rem;
      line-height: 1.5;
      color: #555;
      background-color: #eee;
      border: .075rem solid #ddd;
      border-radius: 0 .25rem .25rem 0; }
    .file-input-container .file-custom:after {
      content: "Choose file..."; }
  .file-input-container .file input {
    min-width: 14rem;
    margin: 0;
    filter: alpha(opacity=0);
    opacity: 0; }

/** float container */
.float-container {
  padding: 0 8px;
  position: relative;
  /** active label */ }
  .float-container input {
    border: none;
    font-size: 16px;
    outline: 0;
    padding: 16px 0 10px;
    width: 100%; }
  .float-container label {
    position: absolute;
    transform-origin: top left;
    transform: translate(0, 16px) scale(1);
    transition: all .1s ease-in-out;
    left: 3px;
    color: #474747;
    font-weight: 100; }
  .float-container.active label {
    transform: translate(0, 4px) scale(0.75);
    font-weight: bold; }
