/* Ripple effect */
.ui-button {
  background-position: center;
  border: none;
  border-radius: 4px;
  padding: 7px 11px;
  font-size: 16px;
  cursor: pointer;
  color: #000000c7;
  outline: none;
  transition: background 0.8s;
  margin: 2px 4px;
  /*Create ripple effec*/ }
  .ui-button[data-style-type=background] {
    border: none;
    box-shadow: 0 0 4px #999; }
  .ui-button[data-style-type=text] {
    border: none; }
    .ui-button[data-style-type=text]:hover {
      background-color: #c5c5c5 !important; }
  .ui-button[data-style-type=outline] {
    border: 1px solid rgba(0, 0, 0, 0.23); }
  .ui-button:hover:not(:disabled) {
    opacity: 0.6; }
  .ui-button:active {
    opacity: 1 !important; }
  .ui-button:disabled {
    cursor: not-allowed; }
  .ui-button.ripple {
    position: relative;
    overflow: hidden;
    transform: translate3d(0, 0, 0); }
  .ui-button.rounded {
    border-radius: 100px !important;
    font-size: 23px;
    min-width: 48px;
    min-height: 48px; }
    .ui-button.rounded[data-style-type=text] {
      min-width: 35px;
      min-height: 30px; }
  .ui-button span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
